import {
  Box,
  createStyles,
  Fab,
  Grid,
  GridSpacing,
  makeStyles,
  Paper,
  Theme,
  Typography,
} from '@material-ui/core';
import React from 'react';
import { OrienteeringEvent } from '../../types/OrienteeringEvent';

import LinearProgress, {
  LinearProgressProps,
} from '@material-ui/core/LinearProgress';
import AddIcon from '@material-ui/icons/Add';
import { Link } from 'react-router-dom';
import MissingCheckpointsArea from '../MissingCheckpointsArea/MissingCheckpointsArea';
import CheckpointCard from '../CheckpointCard/CheckpointCard';
import Solution from '../Solution/Solution';

function LinearProgressWithLabel({
  current,
  max,
  ...props
}: LinearProgressProps & { current: number; max: number }) {
  const value = (100 * current) / max;

  return (
    <Box display="flex" alignItems="center">
      <Box width="100%" mr={1}>
        <LinearProgress variant="determinate" {...props} value={value} />
      </Box>
      <Box minWidth={35}>
        <Typography variant="body2" color="textSecondary">
          {current}/{max}
        </Typography>
      </Box>
    </Box>
  );
}

interface Props {
  event: OrienteeringEvent;
  newCheckpointPath: string;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      padding: 20,
    },
    paper: {
      width: 80,
    },
    item: {
      padding: 5,
    },
    control: {
      padding: theme.spacing(2),
    },
    addCheckpointButton: {
      position: 'absolute',
      right: '1em',
      bottom: '1em'
    },
  })
);

const ScoreLauf = ({ event, newCheckpointPath }: Props) => {
  const checkpoints = event.checkpoints;

  const [spacing] = React.useState<GridSpacing>(2);

  const classes = useStyles();

  return (
    <Box m={1}>
      <Typography variant="h6">{event.name}</Typography>
      <LinearProgressWithLabel
        current={checkpoints.length}
        max={event.checkpoint_count}
      />

      {checkpoints.length > 0 && (
        <Grid container className={classes.root} spacing={2}>
          <Grid item xs={12}>
            <Grid container justify="flex-start" spacing={spacing}>
              {checkpoints.map((checkpoint) => (
                <Grid key={checkpoint.cp_id} item className={classes.item}>
                  <Paper className={classes.paper}>
                    <CheckpointCard
                      checkpoint={checkpoint}
                      eventId={event.id}
                    />
                  </Paper>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
      )}

      <Fab
        className={classes.addCheckpointButton}
        color="primary"
        aria-label="add"
        component={Link}
        to={newCheckpointPath}
      >
        <AddIcon />
      </Fab>
      <MissingCheckpointsArea
        scoredIds={checkpoints.map((ch) => ch.cp_id)}
        max={event.checkpoint_count}
      />
      <Solution checkpoints={checkpoints} max={event.checkpoint_count} />
    </Box>
  );
};

export default ScoreLauf;
